import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import 'quill/dist/quill.snow.css'
import "./assets/aliyun/iconfont.css";
import ElementPlus from 'element-plus';
import 'element-plus/theme-chalk/index.css';
import i18n from '@/lang/i18n'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import '@vueup/vue-quill/dist/vue-quill.snow.css'
import 'highlight.js/styles/xcode.css'
import "./assets/css/edu.css";

let app=createApp(App)

app.directive('my-directive', {
    /* 自定义指令钩子 */
    updated(el, bindings) {
       let title=router.currentRoute.value.meta.title
       if(el.dataset.title){
        document.title=title + '-' + el.dataset.title+'-DEXC区块链学院'
       }else{
        document.title=''+title+'-DEXC区块链学院'
       }
      }

  })


app.use(store as any).use(router as any).use(ElementPlus).use(i18n).mount('#app')

