<script>
import left from './../components/left'
// import { QuillEditor } from '@vueup/vue-quill'
// import hljs from 'highlight.js'
import 'highlight.js/styles/androidstudio.css'

export default {
	name: 'name',
	components: {
		// 组件
		left,
		// QuillEditor,
	},
	data() {
		return {
			title: '文章标题',
			article: '',
			abstract: '',
			// 当前域名
			domain: window.location.href,
			comn: [],
			PurchaseStyle: 'PurchaseStyle',
			purchase: true,
			// editorOption: {
			// syntax: {
			// 	highlight: text => {
			// 		return hljs.highlightAuto(text).value // 这里就是代码高亮需要配置的地方}}
			// 	},
			// },
			// modules: {
			// 	toolbar: null,
			// },
			// },
		}
	},
	methods: {
		// 遍历HTML标签 多余的删除
		// 函数
		async get() {
			let articid = this.$route.params.articid.replace(/.html/g, '')
			let array = await modulega.data(['post=getarticle', { id: articid }])
			let comn = this.$route.params.comn
			console.log(comn)
			comn = await modulega.data(['post=Obtainclassification', comn])
			this.comn = comn['data']
			console.log(this.comn)
			if (array.code == 200) {
				this.article = array['data'][0]
				//
				var reg = new RegExp(`</pre>`, 'g')
				this.abstract = modulega.setText(this.article.content)
				this.title = this.article.title
				// console.log(this.article)
				this.article.content = this.article.content.replaceAll(reg, '<span class="copybox" onclick="copy(event.target)">复制</span></pre>')

				// 需要当前分类
			} else {
				// 这里是错了  跳转到上一页
			}
		},

		// fun() {
		// 	// console.log(Quill)
		// 	console.log(`output->`, this.$refs.myQuillEditor.editor.__quill.root.innerText)
		// 	this.$refs.myQuillEditor.editor.__quill.root.innerText = '1332445'
		// },
	},
	mounted() {
		// 生命周期:
		this.get(), window.scrollTo({ behavior: 'instant', top: 0 })
	},
}
</script>
